body {
  margin: 0;
  font-family: 'Courier New', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear,left top, right top,from(rgb(62, 219, 180)),to(#0e656e));
  background: linear-gradient(90deg,rgb(62, 219, 180) 0%,#0e656e 100%);
}


.App {
  text-align: center;
  
}

.center{
  display: flex;
  justify-content: center;
  /* flex-direction: column; */
}
button{
  cursor: pointer;
}
.particles{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.Tilt{
    background: -webkit-gradient(linear,left top, right top,from(rgb(62, 219, 180)),to(#0e656e));
    background: linear-gradient(90deg,rgb(62, 219, 180) 0%,#0e656e 100%);

}
.form {
  background: radial-gradient(black 3px, transparent 4px), radial-gradient(black 3px, transparent 4px),
    linear-gradient(#fff 4px, transparent 0),
    linear-gradient(
      45deg,
      transparent 74px,
      transparent 75px,
      #a4a4a4 75px,
      #a4a4a4 76px,
      transparent 77px,
      transparent 109px
    ),
    linear-gradient(
      -45deg,
      transparent 75px,
      transparent 76px,
      #a4a4a4 76px,
      #a4a4a4 77px,
      transparent 78px,
      transparent 109px
    ),
    #fff;
  background-size: 109px 109px, 109px 109px, 100% 6px, 109px 109px, 109px 109px;
  background-position: 54px 55px, 0px 0px, 0px 0px, 0px 0px, 0px 0px;
}


.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
 
  display: flex;

  flex-wrap: wrap;

  justify-content: center;
  cursor: pointer;
}
